import React, { ReactNode, HTMLAttributes } from 'react';
import { createComponent } from '../lib';
import type { JSX } from '@vime/core/dist/types';

import { 
  VmVolumeControl,
  VmControl,
  VmIcon,
  VmMuteControl,
  VmTooltip,
  VmSlider 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-volume-control', VmVolumeControl);
define('vm-control', VmControl);
define('vm-icon', VmIcon);
define('vm-mute-control', VmMuteControl);
define('vm-tooltip', VmTooltip);
define('vm-slider', VmSlider);
  

export interface VolumeControlProps extends JSX.VmVolumeControl, HTMLAttributes<HTMLVmVolumeControlElement> {
  children?: ReactNode | ReactNode[]
};

export default createComponent<HTMLVmVolumeControlElement, VolumeControlProps>(
  'vm-volume-control',
  new Set(['lowVolumeIcon','highVolumeIcon','mutedIcon','icons','tooltipPosition','tooltipDirection','hideTooltip','muteKeys','noKeyboard','muted','volume','isMobile','i18n']),
);
  